import React from 'react'

const Feed = (props) => {
    const { id, caption, media_type, media_url } = props.feed
    let post;

    switch (media_type) {
        case "VIDEO":
            post = (
                <>
                    <figure>
                        <video
                            width='100%'
                            height='auto'
                            src={media_url}
                            type="video/mp4"
                            caption={caption}
                            controls playsInline
                        >
                        </video>
                        <figcaption>{caption}.toLowerCase()</figcaption>
                    </figure>
                </>

            )
            break;
        case "CAROUSEL_ALBUM":
            post = (
                <>
                    <figure>
                        <img
                            width='100%'
                            height='auto'
                            id={id}
                            src={media_url}
                            alt={caption}
                            caption={caption}
                        />
                        <figcaption>{caption.toLowerCase()}</figcaption>
                    </figure>
                </>
            );
            break;
        default:
            post = (
                <>
                    <figure>
                        <img
                            width='100%'
                            height='auto'
                            id={id}
                            src={media_url}
                            alt={caption}
                            caption={caption}
                        />
                        <figcaption>{caption.toLowerCase()}</figcaption>
                    </figure>
                </>
            );
    }

    return (
        <>
            {post}
        </>
    );
}

export default Feed;